import { mostrarToast } from '../components/Toast';
import { CadastroFormData, Professor, ApiResponse } from '../types';
import { Validators } from '../utils/validators';

import { FirebaseAuthProvider } from '../../infrastructure/auth/LocalAuthProvider';
import { Usuario } from '../../core/entities/Usuario';

let _authProvider: FirebaseAuthProvider;

export class CadastroService {
  static setAuthProvider(provider: FirebaseAuthProvider) {
    _authProvider = provider;
  }

  static validate(data: CadastroFormData): { isValid: boolean; errors: string[] } {
    return Validators.validateCadastro(data);
  }

  static async cadastrar(data: CadastroFormData): Promise<ApiResponse<Professor>> {
  try {
    if (!_authProvider) {
      throw new Error('Auth provider no foi configurado.');
    }

    // Primeiro, criar usurio no Firebase
    const usuario = await _authProvider.registrar({
      nome: data.nome,
      email: data.email,
      senha: data.senha
    });

    // Depois, notificar o backend para criar o professor no repositrio
    try {
      const response = await fetch('http://localhost:3000/api/professores/cadastro', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          nome: data.nome,
          email: data.email,
          senha: data.senha,
          descricao: data.descricao,
          conteudosDominio: data.conteudosDominio || []
        })
      });

      if (!response.ok) {
        const errorData = await response.json();
        console.warn('Erro no backend, mas usurio criado no Firebase:', errorData);
        // No vamos falhar se o backend no responder, pois o usurio j foi criado no Firebase
      }
    } catch (backendError) {
      console.warn('Erro ao conectar com o backend:', backendError);
      // No vamos falhar se o backend no estiver disponvel
    }

    mostrarToast('Cadastro realizado com sucesso!', 'success');

    return {
      success: true,
      data: {
        id: usuario.id,
        nome: usuario.nome,
        email: usuario.email
      }
    };
  } catch (error: any) {
    console.error('Erro no cadastro:', error);
    
    // Verificar se  um erro especfico do Firebase
    if (error.message.includes('email j est sendo usado') || error.message.includes('email j est cadastrado')) {
      mostrarToast('Este email j est cadastrado. Tente fazer login.', 'warning');
      // Redirecionar para login aps 2 segundos
      setTimeout(() => {
        window.location.href = '/login';
      }, 2000);
    } else if (error.message.includes('senha deve ter pelo menos 6 caracteres')) {
      mostrarToast('A senha deve ter pelo menos 6 caracteres.', 'danger');
    } else if (error.message.includes('Email invlido')) {
      mostrarToast('Por favor, insira um email vlido.', 'danger');
    } else if (error.message.includes('Cadastro com email/senha no est habilitado')) {
      mostrarToast('Erro na configurao do sistema. Entre em contato com o suporte.', 'danger');
    } else {
      mostrarToast(error.message || 'Erro ao cadastrar. Tente novamente.', 'danger');
    }
    
    return { success: false, error: error.message || 'Erro desconhecido' };
  }
}}
